$accent1: #FF6E9C;
$accent2: #F9D439;
$bannerText: #AE4559;

.collection {
    font-family: 'Work Sans', sans-serif;
    margin-top: calc(2vh + 15px);
    font-size: 11pt;

    .title {
        font-weight: 600;
        font-size: calc(12pt + 1vw);
        letter-spacing: 0.5pt;
        color: $bannerText;
        text-transform: lowercase;
    }

    .subtitle {
        font-style: italic;
    }

    .products {
        display: grid;
        width: 100%;
        grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
        column-gap: 10px;
        row-gap: 10px;
        margin-top: 2vh;

        .singleProduct {
            margin: 3px;
            padding: 10px;
            background-color: #f0f0f0;
            border: 1px solid transparent;
            border-top: 1px solid $accent2;
            border-bottom: 1px solid $accent2;
            transition: linear 0.3s;
            box-shadow: 0.7px 1px 4px 0.8px transparent;

            &:hover {
                border: 1px solid #d3d3d3;
                box-shadow: 0.7px 1px 4px 0.8px #bebebe;
            }

            .img {
                width: 100%;
                text-align: center;
                margin-bottom: 10px;
                height: 130px;
                background-color: $bannerText;
                color: white;
                display: grid;
                align-items: center;
                justify-items: center;
                font-family: 'Roboto Slab', serif;
                font-size: 9pt;
                letter-spacing: 2pt;

                img {
                    width: 100%;
                    height: 100px;
                }
            }

            .productTitle {
                font-size: 10pt;
                font-weight: 500;
            }

            .productBrand {
                font-size: 9pt;
                font-style: italic;
                color: #585858;
            }

            .buy {
                display: grid;
                grid-template-columns: auto auto;
                margin-top: 10px;
                align-items: center;
                width: 100%;
                padding: none;

                .add {

                    button.addToCart {
                        padding: 6px;
                        text-transform: lowercase;
                        font-size: 8pt;
                        font-family: 'Work Sans', sans-serif;
                        letter-spacing: 1px;
                        cursor: pointer;
                        transition: 0.5s;
                        font-weight: 500;
                        border: 1px solid $accent2;
                        background-color: #fff6d1;
                        color: #585858;

                        &:focus {
                            outline: none;
                        }

                        &:hover {
                            border-color: $accent1;
                            background-color: #ffecf2;
                        }

                        i {
                            margin-right: 5px;
                        }
                    }
                }

                .price {
                    text-align: right;
                    font-size: 13pt;
                    font-weight: 600;
                    color: $bannerText;
                }
            }
            
        }
    }
}